::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background-color: transparent;
  border-radius: 5px;
}

::-webkit-scrollbar-thumb {
  background-color: rgb(115, 112, 112);
  border-radius: 5px;
}

::-webkit-scrollbar-thumb:hover {
  background-color: rgb(38, 37, 37);
}

.nav-item{
  margin: 0px 1rem;
}
nav{
border-bottom: solid aliceblue 4px;
box-shadow: 1px 1px 2px 2px;
}
.pic{
  width: 25vw; 
  height: 20vh;
  /* border: solid black 2px; */
  flex-shrink: 100;
  transition: 0.8s;
  box-shadow: 1px 0px 10px 8px rgb(34, 34, 34);
}
.colors{
  box-sizing: border-box;
  margin: 5px 0px;
  display: flex;
  overflow: hidden;
  width: 100vw;
  box-shadow: 1px 0px 5px 5px rgb(34, 34, 34);
  box-sizing: border-box;
}
.pic:hover{
  width: 100vw;
}
.green{
  background-color: rgb(51, 198, 140);
}
.red{
  background-color: rgb(210, 70, 70);
}
.blue{
  background-color: rgb(61, 169, 199);
}
.pink{
  background-color: rgb(181, 39, 86);
}
.violet{
  background-color: blueviolet;
}
.yellow{
  background-color: rgb(250, 250, 84);
}
.card{
  width: 18rem;
  opacity: 0.8;
}
.card:hover{
  opacity: 1;
}
.main-fix{
  display: flex;
  flex-direction: column;
}

